const PACKAGE_NAME = '@wix/auto_sdk_payments_site-payment-method-types';

/**
 * Retrieves a payment method type, with some fields localized according to the caller's language and country.
 *
 * The callers language and country are taken from the `languageCode` or `countryCode` parameters in the request.
 * If these parameters are not specified, the values are taken from context of the call if possible. For example, if this method is called from a Wix site, the selected language on that site will be used.
 * If language or country can't be determined, the method doesn't perform any filtering or localization.
 *
 * Language and country are used to determine localized values for the `displayName`, `description` fields.
 * Country is used to determine localized values for the `priority` and `promoted` fields in `merchantFlow` and `buyerFlow`.
 * `currencyCode` is used to determine the eligibility of payment method types for the provided currency.
 */
export function getSitePaymentMethodType(
  payload: object
): RequestOptionsFactory<any> {
  function __getSitePaymentMethodType({ host }: any) {
    const metadata = {
      entityFqdn:
        'wix.payments.site_payment_method_types.v1.site_payment_method_type',
      method: 'GET' as any,
      methodFqn:
        'wix.payments.site_payment_method_types.v1.SitePaymentMethodTypeService.GetSitePaymentMethodType',
      packageName: PACKAGE_NAME,
      url: resolveWixPaymentsSitePaymentMethodTypesV1SitePaymentMethodTypeServiceUrl(
        {
          protoPath: '/v1/site-payment-method-types/{sitePaymentMethodTypeId}',
          data: payload,
          host,
        }
      ),
      params: toURLSearchParams(payload),
    };

    return metadata;
  }

  return __getSitePaymentMethodType;
}

